<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  data () {
    return {
      self: this,
      columns: [
          {
              title: 'ID',
              key: 'id',
              align: 'center',
          },
          {
              title: '组名称',
              key: 'title',
              align: 'center',
          },
          {
            title: '状态',
            key: 'status',
            align: 'center',
            render: (h, params) => {
              const status = params.row.status;
              const color = status == 1 ? 'success' : 'error';
              const text = status == 1 ? '正常' : '停用';

              return h('Tag', {
                props: {
                  type: 'dot',
                  color: color
                }
              }, text);
            }
          },
          {
            title: '排序',
            key: 'sort',
            align: 'center',
          },
          {
            title: '权限管理',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  domProps: { // 修改标签默认内容
                    innerHTML: "编辑"
                  },
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.gotoAuthEdit(params)
                    }
                  }
                }, 'View')
              ]);
            }
          },
          {
            title: '操作',
            key: 'action',
            align: 'center',
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  domProps: { // 修改标签默认内容
                    innerHTML: "编辑"
                  },
                  props: {
                    type: 'primary',
                    size: 'small',
                  },
                  style: {
                    marginRight: '5px'
                  },
                  on: {
                    click: () => {
                      this.gotoAdminGroupEdit(params)
                    }
                  }
                }, 'View'),
                h('Button', {
                  domProps: { // 修改标签默认内容
                    innerHTML: "删除"
                  },
                  props: {
                      type: 'error',
                      size: 'small'
                  },
                  on: {
                      click: () => {
                        this.warnModal = true
                      }
                  }
                }, 'Delete')
              ]);
            }
          }
      ],
      data: [],
      isLoading: true,
      url1: '',
      url2: '',
      totalPages: 0,
      params: {},
      warnModal: false,
      modal_loading: false,
    }
  },
  mounted: function() {
    const token = getToken(),
          params = {"token": token, "page": "0", "per_page": 12};

    this.params = params;
    this.getData(params);
  },
  methods: {
    getData (params) {
      api.adminSort(params).then(res => {
        if(res.code === 0) {
          this.data = res.data.adminGroup
          this.url1 = res.data.url1
          this.url2 = res.data.url2
          this.totalPages = res.data.totlerows
          this.isLoading = false
        } else {
          this.$Notice.error({title: res.msg})
        }
        // console.log(res)
      }).catch(err => {
        this.$Notice.error({title: err})
      })
    },
    gotoAuthEdit (params) {
      const grpid = params.row.id
      this.$router.push({
        path: '/admin/authEdit',
        query: {"grpid": grpid}
      })
    },
    gotoAdminGroupEdit (params) {
      const grpid = params.row.id
      this.$router.push({
        path: '/admin/adminGroupEdit',
        query: {"grpid": grpid}
      })
    },
    changePage: function(page) {
      // console.log(page)
      this.params.page = page;
      this.getData(this.params);
    },
    changePageNum: function(num) {
      // console.log(num)
      this.params.per_page = num;
      this.getData(this.params);
    },
    adminGroupDel: function() {
      this.modal_loading = true
      api.adminGroupDel().then(res => {
        // console.log(res)
        this.warnModal = false
        this.modal_loading = false
        if(res.code == 0) {
          this.$Notice.success({
            "desc": res.msg
          })
        } else {
          this.$Notice.error({
            "desc": res.msg
          })
        }
      }).catch(err => {
        this.warnModal = false
        this.modal_loading = false
        this.$Notice.error({
          "desc": err
        })
      })
    }
  }
}
</script>
<template>
  <div>
    <h3><Icon type="ios-paw" size=24 />管理员分组列表</h3>
    <div class="searchBar">
      <Button type="primary" to="/admin/adminGroupEdit">添加账户</Button>
    </div>
    <Table border :context="self" :columns="columns" :data="data" :loading=isLoading></Table>
    <Modal v-model="warnModal" width="400">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="ios-information-circle" size=20></Icon>
        <span>删除确认</span>
      </p>
      <div style="text-align:center">
        <p>此操作会删除该菜单下所有子菜单。</p>
        <p>确认删除吗？</p>
      </div>
      <div slot="footer">
        <Button type="error" size="large" long :loading="modal_loading" @click="adminGroupDel">Delete</Button>
      </div>
    </Modal>
    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="totalPages*1" show-total show-elevator show-sizer :page-size=12 @on-page-size-change="changePageNum" @on-change="changePage" />
      </div>
    </div>
  </div>
    
</template>
<style lang="scss" scoped>
  .searchBar {
    margin-bottom: 20px;
  }
  .ivu-modal-body {
    p {
      font-size: 16px;
    }
  }
  .ivu-modal-header p {
    font-size: 19px;
  }
</style>
